@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;    /* Include padding and border in the element's total width and height */
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: all 400ms ease
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;                /* Don't wanna show scrollbar */
}

body {
    font-family: 'Poppins', sans-serif;
    background: #1f1f38;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,);
}

/*                         General Styles                      */

.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    height: -webkit-max-content;
    height: max-content;
}

section > h2, section > h5 {
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

section > h2 {
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

a{
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover{
    color: #fff;
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.7rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover{
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}


/*                         Media Query (Medium)                      */

@media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/*                         Media Query (Small)                      */

@media screen and (max-width: 600px) {
    .container {
        width: 90%;
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}
header {
  height: 100vh;
  padding-top: 1rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

.grow {
    display: inline-block;
    -webkit-animation: mymove 2s infinite;
            animation: mymove 2s infinite;
}

@-webkit-keyframes mymove {
    0% {-webkit-transform: scale(1);transform: scale(1);}
    50% {-webkit-transform: scale(1.25);transform: scale(1.25);}
    100% {-webkit-transform: scale(1);transform: scale(1);}
}

@keyframes mymove {
    0% {-webkit-transform: scale(1);transform: scale(1);}
    50% {-webkit-transform: scale(1.25);transform: scale(1.25);}
    100% {-webkit-transform: scale(1);transform: scale(1);}
}

.links {
  margin-top: 1.5rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials ::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.header__image {
  /* border-radius: 12rem 12rem 0 0; */
  border-radius: 50% 50% 0 0;
  background: #f0ca28;
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 2rem;
  overflow: hidden;
  padding: 0.5rem 1rem;
}

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
}

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
    .header__socials, .scroll__down {
        display: none;
    }
}

nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.4rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    font-size: 1.1rem;
    color: var(--color-white);
}

nav a:hover {
    background: rgba(0, 0, 0, 0.4);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}


/*                         Media Query (Small)                      */

@media screen and (max-width: 600px) {
    nav a {
        padding: 0.3rem;
    }
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(8deg);
            transform: rotate(8deg);
    transition: var(--transition);
}

.about__me-image:hover {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}

.aboutImg {
    background: #f0cb28d0;
    width: 100%;
}

.about__content {
    background: var(--color-bg-variant);
    text-align: center;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1rem;
    transition: var(--transition);
    margin: 2rem 0 2.6rem;
}

.about__content:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

@media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }
    #about {
        height: -webkit-max-content;
        height: max-content;
    }
}
.portfolio__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio__item-img {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item-links {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

#portfolio {
    height: -webkit-max-content;
    height: max-content;
}

@media screen and (max-width: 1024px) {
    .portfolio__container {
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}

/*                         Media Query (Small)                      */

@media screen and (max-width: 600px) {
    .portfolio__container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.posts__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
    padding: 2rem;
}

iframe {
    width: 100%;
    height: 90%;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    display: block;
    border-radius: 2rem;
}

@media screen and (max-width: 1024px) {
    iframe {
        width: 95%;
        margin-left: 0;
        height: 95%;
        margin-top: 0;
    }
}

/*                         Media Query (Small)                      */

@media screen and (max-width: 600px) {
    iframe {
        width: 95%;
        margin-left: 0;
        height: 95%;
        margin-top: 0;
    }
    .posts__container {
        grid-template-columns: 1fr;
    }
}

.post {
    background: var(--color-bg-variant);
    padding: 0.5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    height: 50vh;
}
.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap: 5rem;
    gap: 5rem;
}

.contact__option {
    text-align: center;
    border: 1px solid transparent;
    background: var(--color-bg-variant);
    border-radius: 1.2rem;
    transition: var(--transition);
    padding: 1.2rem;
}

.contact__option:hover {
    border-color: var(--color-bg-variant);
    background: transparent;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

.contact__option h5 {
    color: var(--color-light);
}


form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea {
    background: transparent;
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}


/*                         Media Query (Medium)                      */

@media screen and (max-width: 1024px) {
    .contact__container {
        grid-template-columns: 1fr;
    }
    .contact__options {
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/*                         Media Query (Small)                      */

@media screen and (max-width: 600px) {
    .contact__container {
        width: var(--container-width-sm);
        height: -webkit-max-content;
        height: max-content;
    }
}
footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__links {
        display: flex;
        justify-content: center;
        grid-gap: 1rem;
        gap: 1rem;
        margin-bottom: 4rem;
}



@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
    .footer__socials {
        margin-top: 2.6rem;
    }
    .footer__links {
        margin-bottom: 2.6rem;
    }
}
.skills__container {
    display: flex;
    justify-content: center;
}

.skills__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    transition: var(--transition);
    border: 1px solid transparent;
    width: 70vw;
}

.skills__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.skills__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.skills__content {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.skills__details {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.skills__details-icons {
    margin-top: 6px;
    color: var(--color-primary);
}


/*                         Media Query (Medium)                      */

@media screen and (max-width: 1024px) {
    .skills__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .skills__content {
        padding: 1rem;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/*                         Media Query (Small)                      */

@media screen and (max-width: 600px) {
    .skills__container > div {
        width: 100%;
        padding: 2rem 1rem;
        padding-left: 0.3rem;
    }
}
